@import url(https://fonts.googleapis.com/css?family=Montserrat:100,100i,200,200i,300,400&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}



body {
    background-color: aquamarine;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
  }
  
.flex {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
   
}

.flex h1 {
    font-family: Impact, Haettenschweiler, 'Arial Narrow Bold', sans-serif;
}


.header {
    width: 100%;
    height: 60px;
    font-size: 20px;
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;  
}

.header ul {    
    list-style-type: none;
}

.header li {
    position: relative;
    margin: 5px 15px;
    
}

.header a {
    text-decoration: none;
    text-transform: uppercase;
    color: black;
}

.home-page {
    display:-webkit-flex;
    display:flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    margin-top: 30px;
}

.photos {
    margin: 5%;
    border: 1px solid;
    border-radius: 15px;
    padding: 20px;
}

.photos img {
    width: 250px;
    height: auto;
    margin: 15px;
    border-radius: 5px;
    cursor: pointer;
}


*, *::before, *::after {
  box-sizing: border-box;
  margin: 0;
}


.post-feed {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  grid-gap: 10px;
  gap: 10px;
  padding-top: 15px;
  padding-bottom: 15px;
  width: 1000px;
}

.post-container {
    width: 600px;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    padding-top: 15px;
    border: 1px solid;
    border-radius: 10px;
    background-color: #ffffff;
}

.user-avatar {
  min-width: 100px;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  border-radius: 50%;
    width: 50px;
    height: 50px;
}

.user-avatar img {
  width: 55%;
  height: 100%;
  border-radius: 50%;
}


.post-body {
  padding-right: 12.5px;
  font-size: 16px;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  padding-bottom: 20px;
}
.user-info{
  position: relative;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-align-items: baseline;
          align-items: baseline;
  grid-gap: 15px;
  gap: 15px;
  width: 100%;
  font-family: 'Montserrat', sans-serif;
}

.user-name {
  font-weight: bold;
}


.post-text {
  font-family: 'Montserrat', sans-serif;
  margin-top: 15px;
  width: 100%;
}

.post-image {
  border-radius: 15px;
  width: 100%; 
  display: block;
  margin-top: 15px;
}

.post-networks{
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
}

.post-networks img{
  width: 20px;
  margin-top: 15px;
  margin-right: 5px;
}

.network-logo{
  margin-right: 35px;
}


.container {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    border: 1px solid;
    border-radius: 15px;
    background-color: sandybrown;
    width: 300px;
    margin-top: 15px;
    font-family: cursive;
}

.name {
    color: steelblue;
}

.user-input {
    width: 300px;
    margin-top: 10px;
}
